.blue {
  color: #146aa4; }

h1, h2, h3, h4, h5, h6 {
  font-family: Prata, "Times New Roman", serif; }

h2 {
  font-size: 40px;
  margin-bottom: 40px;
  letter-spacing: 1px; }

h3 {
  font-size: 28px;
  margin-bottom: 28px; }

h4 {
  font-size: 20px;
  margin-bottom: 20px; }

p {
  line-height: 1.7; }

p, address {
  font-family: "Open Sans", Helvetica, Arial, sans-serif; }

img {
  max-width: 100%; }

header {
  background: #146aa4;
  padding: 12px 0;
  box-shadow: 0 3px 13px 0 rgba(0, 0, 0, 0.2); }
  header address {
    color: white;
    font-style: italic;
    font-weight: 300;
    font-size: 18px;
    line-height: 24px;
    padding: 0;
    margin: 0; }
  header .phone {
    color: white;
    font-family: Prata, "Times New Roman", serif;
    font-size: 26px; }

section {
  padding: 70px 0; }

section.first-screen {
  background: url("../img/invisalign.png") no-repeat;
  background-size: contain; }
  section.first-screen .first-screen-logo {
    margin-bottom: 35px; }
  section.first-screen h1 {
    text-transform: uppercase;
    color: #146aa4;
    font-size: 56px;
    margin-bottom: 30px; }
  section.first-screen h2 {
    color: #146aa4;
    font-size: 30px;
    letter-spacing: 1px;
    line-height: 1.45;
    margin-bottom: 40px; }
  section.first-screen .desc {
    font-size: 24px; }

section.advantages {
  background: #146aa4;
  color: white; }
  section.advantages h3 {
    font-size: 20px;
    margin-bottom: 20px; }
  section.advantages .desc {
    font-size: 14px; }

section.minus {
  padding-bottom: 35px; }
  section.minus h2 {
    color: #146aa4; }
  section.minus .desc {
    font-size: 18px;
    line-height: 1.7; }

section.doctors {
  padding-top: 35px; }
  section.doctors .desc {
    margin-bottom: 40px; }
  section.doctors h3 {
    color: #146aa4;
    letter-spacing: 1px; }

section.price {
  background: #146aa4;
  color: white; }

footer {
  background: #ecf3f7;
  padding: 55px 0; }
